
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import api from '../server';
export default {
components:{
PopupPicker,
Datetime,
Alert
},
data:function () {
return{
name:'',
sex: [['男', '女']],
sexDefault:[],
relationDefault:[],
birthday:new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate(),
relation:[['自己','父亲','母亲','配偶 ','儿子','女儿','兄弟','姐妹','亲戚','朋友','其他']],
}
},
methods:{
submitform(){

if(!this.name){
this.$vux.alert.show({
content: '姓名不能为空',
})
return false;
}
if(!(/^[\u4E00-\u9FA5]+$/.test(this.name))){
this.$vux.alert.show({
content: '请输入中文',
})
return false;
}
if(!this.sexDefault.length){
this.$vux.alert.show({
content: '请选择性别',
})
return false;
}
if(!this.relationDefault.length){
this.$vux.alert.show({
content: '请选择与您的关系',
})
return false;
}
var patientsDetail={
"name":this.name,
"sex":this.sexDefault.toString(),
"birthday":this.birthday,
"relation":this.relationDefault.toString()
};
api.addpatient(patientsDetail).then(res=>{
if(res.code == '000'){
history.go(-1);
}
})
}
}
}



